<template>
<div>
  <div class="search">
      <el-input v-model="input" clearable size="medium" placeholder="搜索需要加载的新闻标题"></el-input>
     <el-button type="success" @click="searchNews">搜索新闻</el-button>
    </div>
  <el-table
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="新闻ID"
      prop="newsId">
    </el-table-column>
    <el-table-column
      label="新闻标题"
      prop="newsTitle">
    </el-table-column>
   
     
   

    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-button type="primary" @click="chattop">查看置顶新闻</el-button>
         <el-button type="primary" @click="chatAll">查看所有新闻</el-button>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          v-bind:disabled="scope.row.newslsTop == 1"
          v-if="scope.row.newslsTop == 0"
          @click="handleEdit(scope.$index, scope.row)">置顶</el-button>
          <el-button
          size="mini"
          v-bind:disabled="scope.row.newslsTop == 0"
          v-if="scope.row.newslsTop == 1"
          @click="handleEdit1(scope.$index, scope.row)">取消置顶</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">下架</el-button>
      </template>
    </el-table-column>
     
  </el-table>
  <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 14]" 
      :page-size="pagesize"         
      layout="total,sizes, prev, pager, next, jumper"
      :total="total">    
</el-pagination>
</div>
</template>

<script>
export default {
  name: "news",
  data() {
    return {
      total:0,
      input:"",
      currentPage: 1, //初始页
      pagesize: 14, //    每页的数据
      tableData: [],
      search: "",
      isTop:false
    };
  },
  created() {
    this.searchNews()
  },
  methods: {
    chatAll(){
      this.isTop = false
      this.currentPage = 1
      this.searchNews()
      
    },
    chattop(){
      this.isTop = true
      this.$axios
        .get("/backstage/TopNews/"+this.currentPage+"/"+this.pagesize+"/"+1)
        .then(res => {
          this.tableData = res.data.rows
          this.total = res.data.total
        });
    },
    searchNews(){
      var url = ""
       var data = {
        page:1,
        rows:14,
        newsTitle:""
      }
      if (this.input == "") {
         url ="/backstage/seeNews?page="+this.currentPage+"&rows="+this.pagesize
      }else{
        url = "/backstage/seeNews?page="+this.currentPage+"&rows="+this.pagesize+"&newsTitle="+this.input
      }
      // console.log(this.input)
     
       this.$axios
        .post(url)
        .then(res => {
          // console.table(res.data.rows);
          this.tableData = res.data.rows
          this.total = res.data.total
          // created()
          
        });
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange: function(size) {
      this.pagesize = size;
      // console.log(this.pagesize); //每页下拉显示数据
      this.searchNews()
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage); //点击第几页
      if (this.isTop) {
        this.chattop()
      }else{
        this.searchNews()
      }
      
    },
    
    handleEdit(index, row) {
      // console.log(row);
      this.$axios
        .get("/backstage/newsStick/"+row.newsId)
        .then(res => {
          // console.log(res);
         if (res.status == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
         }
          // created()
          this.searchNews()
        });
    },
    handleEdit1(index, row) {
      // console.log(row);
      this.$axios
        .get("/backstage/abrogate/"+row.newsId)
        .then(res => {
          // console.log(res);
         if (res.status == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
         }
          // created()
          this.searchNews()
        });
    },
    handleDelete(index, row) {
      this.$axios
        .get("/backstage/shallDown/"+row.newsId)
        .then(res => {
          // console.log(res);
         if (res.status == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
         }
          // created()
          this.searchNews()
        });
    }
  }
};
</script>
<style scoped>
.search {
  width: 75%;
  margin: 30px auto;
}
.search .el-input {
  width: 90%;
}
</style>



