<template>
  <div>
    <div class="search">
      <el-input v-model="input" clearable size="medium" placeholder="输入userid"></el-input>
     <el-button type="success" @click="searchUser">搜索ID</el-button>
    </div>
    <el-table
    :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="管理员ID"
      prop="adminId">
    </el-table-column>
    <el-table-column
      label="管理员"
      prop="adminName">
    </el-table-column>
    
    <el-table-column
      label="第一权限"
      prop="roles[0].roleName">
    </el-table-column>
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
         <el-button
          size="medium "
          type="primary"
          @click="addAdmin">添加管理员
          </el-button>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">修改</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  

  <el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  :before-close="handleClose">
    <el-form ref="form" :model="form" label-width="80px">
    
    <el-form-item label="管理员">
      <el-input v-model="form.adminName"></el-input>
    </el-form-item>
    <el-form-item label="管理密码">
      <el-input v-model="form.adminPassword"></el-input>
    </el-form-item>
    </el-form>
     <div v-if="!isAdd">
       <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" >全选</el-checkbox>
      <div style="margin: 15px 0;"></div>
      <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
        <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
      </el-checkbox-group>
     </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    
    <el-button v-if="isAdd" type="primary" @click="updata">添加</el-button>
    <el-button  v-else type="success" @click="fixdata">确 定</el-button>
  </span>
</el-dialog>




 
    </div>

</template>

<script>
const cityOptions = [
  "管理新闻权限",
  "管理用户权限",
  "管理评论权限",
  "赋予管理员权限"
  
];
export default {
  name: "admin",
  data() {
    return {
      isAdd: false,
      checkAll: false,
      checkedCities: [],
      cities: cityOptions,
      isIndeterminate: true,
      form: {},
      input: "",
      tableData: [],
      search: "",
      dialogVisible: false
    };
  },
  methods: {
    fixdata() {
      // console.log(cityOptions)
      // // this.cities = cityOptions
      // console.log(this.checkedCities);

      var adminIdPojo = {
        roleId1: "string",
        roleId2: "string",
        roleId3: "string",
        roleId4: "string"
      };
      for (let index = 0; index < this.checkedCities.length; index++) {
        if (this.checkedCities[index] == "管理新闻权限") {
          adminIdPojo.roleId1 = "1";
        } else if (this.checkedCities[index] == "管理用户权限") {
          adminIdPojo.roleId2 = "2";
        } else if (this.checkedCities[index] == "管理评论权限") {
          adminIdPojo.roleId3 = "3";
        } else if (this.checkedCities[index] == "赋予管理员权限") {
          adminIdPojo.roleId4 = "4";
        }
      }
      // console.table(adminIdPojo)
      this.$axios
        .post("/roles/update?adminId=" + this.form.adminId, adminIdPojo)
        .then(res => {
          // console.log(res);
          // created()
          if (res.status == 200) {
            this.getFdata();
            this.dialogVisible = false;
            this.$message({
              message: "修改成功",
              type: "success"
            });
          } else {
            this.$message.error("修改失败");
          }
        });
        //  console.log(this.checkedCities);
    },
    updata() {
      // console.log(this.form);
      // console.log(this.checkedCities);
      // console.log(this.checkedCities.length);
      var data = {
        adminId: this.form.adminId,
        adminName: this.form.adminName,
        adminPassword: this.form.adminPassword,
        roles: []
      };
      var root = {
        roleId: "string",
        roleName: "string"
      };
      for (let index = 0; index < this.checkedCities.length; index++) {
        var id = "0";
        if (this.checkedCities[index] == "管理新闻权限") {
          var id = "1";
        } else if (this.checkedCities[index] == "管理用户权限") {
          var id = "2";
        } else if (this.checkedCities[index] == "管理评论权限") {
          var id = "3";
        } else if (this.checkedCities[index] == "赋予管理员权限") {
          var id = "4";
        }
        var root = {
          roleId: id,
          roleName: this.checkedCities[index]
        };
        data.roles.push(root);
      }
      var _this = this
      // console.log(data);
      this.$axios.post("/biyeAdmim/savaAdmin", data).then(res => {
        // console.log(res);
        if (res.data.status == 200) {
          _this.dialogVisible= false
          _this.getFdata()
        }
      });
    },
    addAdmin() {
      this.form = {};
      this.checkedCities = [];
      this.dialogVisible = true;
      this.isAdd = true;
    },
    handleEdit(index, row) {
      this.isAdd = false;
      // console.log(row)
      this.dialogVisible = true;
      this.form = row;
      // console.log(this.form)
      this.checkedCities = []
      for (let index = 0; index < row.roles.length; index++) {
        this.checkedCities.push(row.roles[index].roleName);
      }
    },
    handleCheckAllChange(val) {
      this.checkedCities = val ? cityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },

    searchUser() {
      // console.log(this.input);
      this.$axios.get("/biyeAdmim/selectAdminId/" + this.input).then(res => {
        console.log(res.data);
        if (res.data == "") {
          this.$message.error("查询不到该管理员，请检查ID");
          // console.log(res.data);
          this.tableData = [];
          // return
        } else {
          this.tableData = [];
          this.tableData.push(res.data[0]);
        }
        // console.log(this.tableData)
        // this.tableData = res.data;
      });
    },

    handleDelete(index, row) {
      // console.log(row)
      this.$confirm("确认删除？")
        .then(() => {
          
          this.$axios
            .get("/biyeAdmim/deleteAdminId/" + row.adminId)
            .then(res => {
              // console.log(res);
              this.getFdata();
            })
            .catch(() => {
              console.log("err");
              this.$message.error("删除失败，请检查");
            });
        })
        .catch(() => {
          console.log("err");
        });
      // console.log(row.userId);
    },
    getFdata() {
      this.$axios.get("/roles/selectAdminRoles").then(res => {
        // console.log(res.data);
        this.tableData = res.data;
        // console.log(this.tableData)
      });
      
    }
    // handleClose(done) {
    //   this.$confirm("确认关闭？")
    //     .then(_ => {
    //       done();
    //     })
    //     .catch(_ => {});
    // }
  },

  created() {
    this.getFdata();
  }
};
</script>
<style scoped>
.search {
  width: 75%;
  margin: 30px auto;
}
.search .el-input {
  width: 90%;
}
</style>



