<template>
<div>
  <!-- <div class="search">
      <el-input v-model="input" clearable size="medium" placeholder="搜索需要加载的新闻标题"></el-input>
     <el-button type="success" @click="searchNews">搜索新闻</el-button>
    </div> -->
  <el-table
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="评论时间"
      prop="commentDate">
    </el-table-column>
    <el-table-column
      label="评论ID"
      prop="commentId">
    </el-table-column>
    <el-table-column
      label="新闻ID"
      prop="newsId">
    </el-table-column>
     <el-table-column
      label="评论内容"
      prop="commentDesc">
    </el-table-column>
     <el-table-column
      label="点赞数"
      prop="likeNum">
    </el-table-column>
     <el-table-column
      label="用户ID"
      prop="biyeUserComment.userId">
    </el-table-column>
     
   

    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-button type="success" @click="searchNews">刷新</el-button>
      </template>
      <template slot-scope="scope">
        <!-- <el-button
          size="mini"
          v-bind:disabled="scope.row.newslsTop == 1"
          @click="handleEdit(scope.$index, scope.row)">置顶</el-button> -->
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
     
  </el-table>
  <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10]" 
      :page-size="pagesize"         
      layout="total,sizes, prev, pager, next, jumper"
      :total="total">    
</el-pagination>
</div>
</template>

<script>
export default {
  name: "comment",
  data() {
    return {
      total:0,
      input:"",
      currentPage: 1, //初始页
      pagesize: 10, //    每页的数据
      tableData: [],
      search: ""
    };
  },
  mounted(){
   
  },
  created() {
    this.searchNews()
  },
  methods: {
    searchNews(){
      var url = "/BiyeComment/selectall/"+this.currentPage+"/"+this.pagesize
       this.$axios
        .get(url)
        .then(res => {
          // console.log(res.data);
          this.tableData = res.data.rows
          this.total = res.data.total
          // created()
          
        });
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange: function(size) {
      this.pagesize = size;
      // console.log(this.pagesize); //每页下拉显示数据
      this.searchNews()
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage); //点击第几页
      this.searchNews()
    },
    
    handleEdit(index, row) {
      // console.log(row);
      this.$axios
        .get("/backstage/newsStick/"+row.newsId)
        .then(res => {
          // console.log(res);
         if (res.status == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
         }
          // created()
          this.searchNews()
        });
    },
    handleDelete(index, row) {
      // console.log(row.commentId)
      this.$axios
        .post("/BiyeComment/delete/"+row.commentId)
        .then(res => {
          // console.log(res);
         if (res.status == 200) {
            this.$message({
              message: "删除成功",
              type: "success"
            });
         }
          // created()
          this.searchNews()
        });
    }
  }
};
</script>
<style scoped>
.search {
  width: 75%;
  margin: 30px auto;
}
.search .el-input {
  width: 90%;
}
</style>



