import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import user from '@/components/children/user'
import admin from "@/components/children/admin"
import echarts from "@/components/children/echarts"
import news from "@/components/children/news"
import comments from "@/components/children/comments"
import login from "@/components/login"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/login',
      meta: {
        requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
    },
      children: [
        {path: 'user', name: 'user', component:user,meta: {requireAuth: true}},
        {path: 'admin', name: 'admin', component:admin,meta: {requireAuth: true}},
        {path: 'echarts', name: 'echarts', component:echarts},
        {path:"news",name:"news",component:news,meta: {requireAuth: true}},
        {path:"comments",name:"comments",component:comments,meta: {requireAuth: true}}
      ]
    },{
      path:"/login",
      name:"login",
      component:login,

    }

  ]
})



