<template>
  <div class="box">
    <img src="./../img/新易logo.png" class="logo">
    <div class="left">
       <el-menu
        :default-active="route"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="height:100%"
        router
        >
      <el-menu-item index="echarts">
        <i class="el-icon-menu"></i>
        <span slot="title">数据量</span>
      </el-menu-item>
      <el-menu-item index="user" v-if="this.roles.user">
        <i class="el-icon-menu"></i>
        <span slot="title">用户管理</span>
      </el-menu-item>
     <el-menu-item index="admin" v-if="this.roles.admin">
        <i class="el-icon-menu"></i>
        <span slot="title">管理员管理</span>
      </el-menu-item>
      
      <el-menu-item index="news" v-if="this.roles.news">
        <i class="el-icon-menu"></i>
        <span slot="title">新闻管理</span>
      </el-menu-item>
      <el-menu-item index="comments" v-if="this.roles.comments">
        <i class="el-icon-menu"></i>
        <span slot="title">评论管理</span>
      </el-menu-item>
      <el-menu-item >
        
        <span slot="title" @click="showtime">{{realTime}}</span>
      </el-menu-item>
       <el-menu-item >
        
        <span slot="title" @click="showtime">你好，{{user}}</span>
      </el-menu-item>
      <el-menu-item @click="show">
        <span slot="title" >退出登入</span>
      </el-menu-item>
    </el-menu>
    
    </div>
    <div class="right">
      <router-view></router-view>
    </div>
  <el-dialog
  title="提示"
  :visible.sync="centerDialogVisible"
  width="30%"
  center>
  <span>确定退出吗？</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="exit">确 定</el-button>
  </span>
</el-dialog>

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      centerDialogVisible: false,
      route: "echarts",
      realTime: "",
      roles: {
        user: 0,
        root: 0,
        admin: 0,
        news: 0,
        comments: 0
      }
    };
  },
  mounted() {
    this.getRoot();
    // 页面加载完后显示当前时间
    this.realTime = this.dealWithTime(new Date());
    // 定时刷新时间
    let _this = this;
    // 定时器
    this.timer = setInterval(function() {
      _this.realTime = _this.dealWithTime(new Date()); // 修改数据date
    }, 1000);
  },
  methods: {
    getRoot() {
      this.$axios
        .get("/biyeAdmim/selectAdminId/" + this.$store.getters.id)
        .then(res => {
          // console.log(res.data[0].roles);
          for (let index = 0; index < res.data[0].roles.length; index++) {
            // console.log(res.data[0].roles[index].roleId);
            // console.log(res.data[0].roles[index].roleName);
            if (res.data[0].roles[index].roleId == "1") {
              this.roles.news = 1;
            } else if (res.data[0].roles[index].roleId == "2") {
              this.roles.user = 1;
            } else if (res.data[0].roles[index].roleId == "3") {
              this.roles.comments = 1;
            }else if (res.data[0].roles[index].roleId == "4") {
              this.roles.admin = 1;
            }
          }
          // console.log(this.roles)
          this.$store.dispatch("setRouterRoot",this.roles);
        });
    },
    dealWithTime(data) {
      let formatDateTime;
      let Y = data.getFullYear();
      let M = data.getMonth() + 1;
      let D = data.getDate();
      let H = data.getHours();
      let Min = data.getMinutes();
      let S = data.getSeconds();
      let W = data.getDay();
      H = H < 10 ? "0" + H : H;
      Min = Min < 10 ? "0" + Min : Min;
      S = S < 10 ? "0" + S : S;
      switch (W) {
        case 0:
          W = "天";
          break;
        case 1:
          W = "一";
          break;
        case 2:
          W = "二";
          break;
        case 3:
          W = "三";
          break;
        case 4:
          W = "四";
          break;
        case 5:
          W = "五";
          break;
        case 6:
          W = "六";
          break;
        default:
          break;
      }
      formatDateTime =
        Y + "年" + M + "月" + D + "日 " + H + ":" + Min + ":" + S + " 星期" + W;
      return formatDateTime;
    },
    exit() {
      this.$store.dispatch("exit");
    },
    show() {
      this.centerDialogVisible = true;
      // if (confirm("你确定退出吗？")) {
      //
      // }
    },
    showtime() {},
    test() {
      // console.log(123);
      this.$store.dispatch("test", "dadada");
      // console.log(this.getArr);
    },
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  created() {
    this.route = this.$route.path.replace("/", "");
  },
  computed: {
    getArr() {
      return this.$store.getters.getArr;
    },
    user() {
      return this.$store.getters.user;
    },
    id() {
      return this.$store.getters.id;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  width: 100%;
  height: 100%;
}
.box {
  width: 100%;
  height: 100%;
  display: flex;
}
.left {
  flex: 1.5;
  height: 100%;
  /* width: 15%; */
  /* background: rgb(84, 92, 100) */
}
.right {
  /* width: 85%; */
  flex: 8.5;
  height: 100%;
}
.logo {
  position: fixed;
  left: 60px;
  bottom: 25px;
  width: 85px;
  height: 85px;
  z-index: 9;
}
</style>



