<template>
  <div>
    <div class="search">
      <el-input v-model="input" clearable size="medium" placeholder="输入userid"></el-input>
     <el-button type="success" @click="searchUser">搜索ID</el-button>
    </div>
    <el-table
    :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="用户ID"
      prop="userId">
    </el-table-column>
    <el-table-column
      label="用户名"
      prop="username">
    </el-table-column>
    
    <el-table-column
      label="手机号码"
      prop="phone">
    </el-table-column>
    <el-table-column
      label="邮箱"
      prop="email">
    </el-table-column>
    <el-table-column
      label="标识"
      prop="opendId">
    </el-table-column>
     <el-table-column
      label="用户身份"
      prop=graceId>
    </el-table-column>
    <el-table-column
      label="注册时间"
      prop="created">
    </el-table-column>
    <el-table-column
      label="上传时间"
      prop="updated">
    </el-table-column>
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <!-- <el-button type="success" @click="searchUser">123</el-button> -->
      </template>
      <template slot-scope="scope">
        <!-- <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">查看</el-button> -->
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 14]" 
      :page-size="pagesize"         
      layout="total,sizes, prev, pager, next, jumper"
      :total="total">    
</el-pagination>

 
    </div>

</template>

<script>
export default {
  name: "user",
  data() {
    return {
      total: 0,
      input: "",
      isTop: false,
      currentPage: 1, //初始页
      pagesize: 14, //    每页的数据
      input: "",
      tableData: [],
      search: "",
      dialogVisible: false,
      dialogText: {
        title: "用户信息"
      }
    };
  },
  methods: {
    searchAlluser() {
      // console.log("/biyeUser/selectBiyeUserAll/" +this.currentPage +"/" +this.pagesize)
      this.$axios
        .get("/biyeUser/selectBiyeUserAll/" +this.currentPage +"/" +this.pagesize)
        .then(res => {
          // console.log(res);
          this.tableData = res.data.rows
          this.total = res.data.total
        });
    },
    handleSizeChange: function(size) {
      this.pagesize = size;
      // console.log(this.pagesize); //每页下拉显示数据
      this.searchAlluser();
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage); //点击第几页
      if (this.isTop) {
        this.chattop();
      } else {
        this.searchAlluser();
      }
    },
    searchUser() {
      // console.log(this.input)
      this.$axios.get("/biyeUser/selectUserId/" + this.input).then(res => {
        // console.log(res.data.length);
        // console.log(res);
        if (res.data == "") {
          this.$message.error("查询不到该用户，请检查ID");
          // console.log(res.data);
          this.tableData = [];
          // return
        } else {
          this.tableData = [];
          this.tableData.push(res.data);
          this.fixuresdata();
        }
        // console.log(this.tableData)
        // this.tableData = res.data;
      });
    },

    handleDelete(index, row) {
      this.$confirm("确认删除？").then(() => {
        // console.log(row.userId);
        this.$axios
          .get("/biyeUser/cancelUserId/" + row.userId)
          .then(res => {
            // console.log(res);
            if (res.data.status == 200) {
              this.$message({
                message: "删除成功",
                type: "success"
              });
              this.searchAlluser();
            } else if (res.data.status == 500) {
              this.$message.error("数据库错误请联系管理员");
            } else {
              this.$message.error("删除失败，请检查！");
            }
          })
          .catch(() => {
            // console.log("err");
          });
      });
    },
    fixuresdata() {
      // console.log(this.tableData[0].graceId);
      if (this.tableData[0].graceId == "1") {
        this.tableData[0].graceId = "用户";
      } else if (this.tableData[0].graceId == "2") {
        this.tableData[0].graceId = "记者";
      }
    }
  },
  mounted() {
    this.searchAlluser();
  },
  created() {
    //开启自动查询所有
    // this.$axios.get("/biyeUser/selectBiyeUserAll").then(res => {
    //   console.log(res.data)
    //   // console.log(this.tableData)
    //   this.tableData = res.data;
    // });
  }
};
</script>
<style scoped>
.search {
  width: 75%;
  margin: 30px auto;
}
.search .el-input {
  width: 90%;
}
</style>



