import Vue from 'vue';
import Vuex from 'vuex';
import axios from "axios"
import md5 from 'js-md5';
import router from './../router'
Vue.use(Vuex);
 const state={   //要设置的全局访问的state对象
    arr:[],
    // islogin:false
    token:false,
    user:'',
    id:0,
    roles: {},
    loding:{
      status:0,
      msg:""
    }
};
const getters = {   //实时监听state值的变化(最新状态)
    loding(){
      return state.loding
    },
    getArr () {
      return state.arr
    },
    user(){
      return state.user
    },
    id(){
      return state.id
    }
};
const actions = {
  setRouterRoot(ctx,data){
    // console.log(data)
  },
  exit(ctx){
    ctx.commit("exit")
    router.push("/login")
  },
   test (ctx, data) {
    // console.log(data)
    
    
    var passwordMd5 = md5(data.password)
    var _this = this
    // console.log(userMd5)
    // console.log(passwordMd5)
    console.log("/sso/LoginAdmin/"+data.user+"/"+passwordMd5)
    axios.post("/sso/LoginAdmin/"+data.user+"/"+passwordMd5).then(res=>{
      console.log(res)
      // console.log(res.data.data.adminId)
      // console.log(res.data.data.adminName)
      
      if (res.data.status == 200) {
        var all = {
          id:res.data.data.adminId,
          name:res.data.data.adminName
        }
        ctx.commit('test',all)
        ctx.commit("s")
        router.push("/echarts")
      }else{
        console.log(1)
        var err= {
          status:res.data.status,
          msg:res.data.msg
        }
        console.log(err)
        ctx.commit('err', err)
      }
    })
    ctx.commit('test', data)
   },
};
const mutations = {
  s(state){
    state.loding = []
  },
  err(state,data){
    state.loding = data
  },
  exit(state){
    state.token = false
    
  },
  test (state, data) {
    // state.arr.push(data)
    state.token = true
    // console.log(state.arr)
    state.id = data.id
    state.user = data.name
  }
};

  const store = new Vuex.Store({
       state,
       getters,
       mutations,
       actions
});
export default store;


