<template>
  <div class="chart">
    
    <el-row>
      <el-col :span="8">
        <div class="grid-content bg-purple boyi">
           新闻数量:{{newsNum}}
        </div>
         
      </el-col>
      <el-col :span="8">
        
         <div class="grid-content bg-purple-light boer">
          用户数量:{{userNum}}
        </div>
      </el-col>
      <el-col :span="8">
         <div class="tainqi">
      <div class="tianpi-box">
        <div class="box">
          <img src="../../img/yu.png">
        </div>
        <div class="box">
          天气预报
        </div>
        <div class="box">
          城市：{{tianqi.city}}
        </div>
        <div class="box" >                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            
          日期：{{tianqi.all[0].date}}
        </div>
        <div class="box">
          星期：{{tianqi.all[0].week}}
        </div>
        <div class="box">
          当前温度：{{tianqi.all[0].tem}}
        </div>
        <div class="box">
          天气预报：{{tianqi.all[0].wea}}
        </div>
        <div class="box">
          最高温度：{{tianqi.all[0].tem1}}
        </div>
        <div class="box">
          最低温度：{{tianqi.all[0].tem2}}
        </div>
        <div class="box">
          更新时间：{{tianqi.updateTime}}
          </div>
      </div>
    </div>
      </el-col>
    </el-row>
   <el-row>
     <el-col :span="24">
       <div class="jieshi">新易NewsOA系统解释权归新易News研发团队所有@2019.6.4</div>
     </el-col>
   </el-row>
    
  </div>
</template>

<script>
export default {
  name: "echarts",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      newsNum: 0,
      userNum: 0,
      imgsrc: "",
      tianqi: {
        city: "",
        updateTime: "",
        all: []
      }
    };
  },
  mounted() {
    var _this = this;
    this.$axios.get("https://www.tianqiapi.com/api/?version=v1").then(res => {
      console.log(res.data);
      _this.tianqi.city = res.data.country + "，" + res.data.city;
      _this.tianqi.updateTime = res.data.update_time;
      _this.tianqi.all = res.data.data;
    });
    this.$axios.get("/collectNews/selectNum").then(res => {
      console.log(res);
      _this.newsNum = res.data.newsNum;
      _this.userNum = res.data.userNum;
    });
  },
  methods: {}
};
</script>
<style scoped>
.jieshi {
  margin-top: 450px;
  color: #dddddd;
}
.tainqi {
  width: 100%;
  color: aliceblue;
  margin-top: 200px;
  box-sizing: border-box;
}
.tianqi-box {
  margin: 0 auto;
  width: 300px;
  display: flex;
}
.box {
  flex: 1;
}
.chart {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
  background: url("../../img/bg.jpg");
}
.boyi {
  margin-top: 150px;
  margin-left: 255px;
  width: 300px;
  height: 300px;
  background: url("../../img/波1.png");
  box-sizing: border-box;
  line-height: 300px;
  color: aliceblue;
  font-size: 26px;
}
.boer {
  box-sizing: border-box;
  margin-top: 150px;
  margin-left: 255px;
  width: 300px;
  height: 300px;
  background: url("../../img/波2.png");
  line-height: 300px;
  color: aliceblue;
  font-size: 26px;
}
</style>



