<template>
    <div class="login">
        <div class="login-box">
            <p>登录</p>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="管理账号">
                    <el-input v-model="form.user" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="管理密码">
                    <el-input v-model="form.password" show-password placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" size="medium " @click="login">登入管理</el-button>
                    <el-button type="warning" size="medium " @click="forget">忘记密码</el-button>
                </el-form-item>
            </el-form>
             
        </div>
    </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
         labelPosition: 'left',
        form: {
          user:"",
          password:""
        }
    };
  },
  methods:{
      login(){
    //   console.log(this.form)
    if (this.form.user!= ""&&this.form.password != "") {
        this.$store.dispatch("test",this.form );
       setTimeout(()=>{
            console.log(this.$store.getters.loding)
            if (this.$store.getters.loding.status == 400||this.$store.getters.loding.status == 500) {
                this.$message.error(this.$store.getters.loding.msg);
            }
        },1000)
    }else{
         this.$message({
          showClose: true,
          message: '输入有误',
          type: 'error'
        });
    }
      
  },
  forget(){
        this.$message({
                message: "请联系管理员",
                type: "success"
                });
  }
  },
  computed:{
      
  }

};
</script>
<style scoped>
* {
 
  box-sizing: border-box;
}
.login {
  width: 100%;
  height: 100%;
  background: red;
  background: url("./../img/bg.jpg");
  position: relative;
}
.login-box {
  position: absolute;
  left: 42%;
  top: 37%;
  background: #ffffff;
  width: 350px;
  padding: 18px;
  border-radius: 18px
}
.login-box p{
   text-align: center;
   
   font-size: 28px;
   
}
</style>




